
body, #root {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
h1 {
  text-align: center;
  margin: 0;
  margin-top: 15px;
}
p {
  text-align: center;
}

.states {
  stroke: #fff;
  stroke-width: 0.5px;
  stroke-linejoin: round;
  stroke-linecap: round;
}
.states:hover {
  fill: yellow;
}
.counties {
  fill: none;
  stroke: black;
  stroke-width: .06px;
}

.d3-tip {
  font-family: "Open Sans", sans-serif;
  font-size: 10pt;
  padding: 10px;
  background: #161616;
  color: lightgray;
  border-radius: 15px;
}
.d3-tip div {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.d3-tip div p:nth-of-type(2n) {
  color: red;
  margin-left: 5px;
}
.d3-tip p {
  margin: 0;
}

#menu {
  margin-top: 25px;
  font-size: 1.1rem;
  text-align: center;
}

